<!-- Created by init-app.js at Tue Mar 10 2020 16:02:41 GMT+0100 (GMT+01:00) -->
<template>
  <wgu-app-tpl>
    <!-- insert your app slots here  -->
  </wgu-app-tpl>
</template>

<script>
  import Vue from 'vue'
  import WguAppTemplate from './WguAppTemplate.vue';
  import { WguEventBus } from '../src/WguEventBus';
  import VectorLayer from 'ol/layer/Vector'
  import VectorSource from 'ol/source/Vector';
  import Feature from 'ol/Feature';
  import Point from 'ol/geom/Point';
  import {transform} from 'ol/proj.js';
  import {Circle as CircleStyle, Fill, Stroke, Style} from 'ol/style';
  import {circular as circularPolygon} from 'ol/geom/Polygon';
  import {inAndOut} from 'ol/easing';

  export default {
    name: 'my-wgu-app',
    components: {
      'wgu-app-tpl': WguAppTemplate
    },
    data () {
      return {
        baseColor: Vue.prototype.$appConfig.baseColor
      }
    },
    created () {
      const me = this;
      WguEventBus.$on('ol-map-mounted', olMap => {
        me.map = olMap;

        let map = me.map;

        let styles = [
          new Style({
            stroke: new Stroke({
              color: me.baseColor,
              width: 6
            }),
            fill: new Fill({
              // TODO: find better approach for making transparent
              color: me.baseColor + '33'
            })
          }),
          new Style({
            image: new CircleStyle({
              radius: 7,
              fill: new Fill({
                color: me.baseColor
              })
            }),
            geometry: function (feature) {
              return feature.get('centerPoint');
            }
          })
        ];

        // add empty vector layer
        let radiusSource = new VectorSource({});
        const radiusLayer = new VectorLayer({
          name: 'radius-layer',
          lid: 'radius-layer',
          displayInLayerList: true,
          visible: true,
          source: radiusSource,
          style: styles
        });
        map.addLayer(radiusLayer);

        // handle click on map
        map.on('singleclick', function (evt) {
          var coord3857 = map.getCoordinateFromPixel(evt.pixel);
          let radius = 15000; // 15 kilometer

          const coord4326 = transform(coord3857, 'EPSG:3857', 'EPSG:4326');
          let circle4326 = circularPolygon(coord4326, radius, 64);
          let circle3857 = circle4326.clone().transform('EPSG:4326', 'EPSG:3857');

          radiusSource.clear();
          map.renderSync();
          radiusSource.addFeature(new Feature({
            geometry: circle3857,
            centerPoint: new Point(coord3857)
          }));
          map.renderSync();

          // zoom to feature
          map.getView().fit(circle3857, {
            maxZoom: 10,
            duration: 3000,
            easing: inAndOut
          });
        });
      });
    }
  }
</script>



